import React, { Children, useEffect, useState } from 'react'
import Rating from '../rating/Rating'
import moment from 'moment'
const CommentCard = ({comment,children}) => {
    const {name,images} = comment.user[0]
    const [image,setImage] = useState('')
    useEffect(()=>{
        if(images){
            setImage(images)
        }
    },[image])
  return (
    <div className='comment_card'>
      <div className='comment_card_row'>
       {<h3>{name}</h3> }
        { <img className='myImg' src={images?image.url:'data:image/png;base64,'} alt={name}/> }
        

        {
            comment.rating!==0 && <Rating props ={comment}/>
        }
        
      </div>
      <span>{new Date(comment.createdAt).toLocaleString()}</span>
      <span>{moment(comment.createdAt).fromNow()}</span>
      <p dangerouslySetInnerHTML={{__html:comment.content}}></p>
      {children}
    </div>
  )
}

export default CommentCard
